import React from 'react';
import { Grid, Cell } from 'react-mdl'

class About extends React.Component {
    render() {
        return (
            <div className="about-body">
                <Grid>
                    <Cell col={12}>
                        Codinf
                        </Cell>
                        <Cell col={12}>
                            Codin
                            </Cell>
                </Grid>
                <hr style={{ borderTop: '3px' }} />
            </div>
        )
    }
}

export default About;